h1 {
  font-size: 18px;
  text-align: left;
}

.image-fluid {
  display: block;
  margin: 0 auto;
  height: 180px;
}

.product-container {
  display: flex;
  flex-wrap: wrap;
  background: #f1f0f0;
  justify-content: space-between;
  padding: 0 5rem;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #c6c6c6;
  padding: 10px;
}

.pagination button {
  background-color: black;
  margin-right: 15px;
  font-size: 15px;
  color: white;
  font-family: "Kanit", sans-serif;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}

.pagination button:hover {
  background-color: white;
  color: black;
}

.card-container {
  width: 100%;
  padding: 1.25rem;
}

@media (min-width: 768px) {
  .card-container {
    width: 50%;
  }
}

@media (min-width: 992px) {
  .card-container {
    width: 33.3333%;
  }
}
