.footer-content {
  width: 100%;
  min-height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  background-color: #333;
  font-size: 14px;
  color: #fff;
}

.footer-logo-icons-container {
  width: 100%;
  min-height: 25px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

@media (max-width: 576px) {
  .footer-logo-icons-container {
    margin-top: 25px;
    flex-wrap: wrap;
    flex-direction: column;
  }
}

.footer-logo {
  flex-basis: 50%;
  max-width: 50%;
}

.footer-logo-descripcion-container {
  flex-basis: 50%;
  max-width: 50%;
}

.footer-logo-descripcion-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 15px;
}

.footer-icons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-basis: 50%;
  max-width: 50%;
}

.icons-container {
  display: flex;
  gap: 2rem;
  font-size: 3rem;
}

@media (max-width: 768px) {
  .icons-container {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.icons-container i {
  cursor: pointer;
}

@media (max-width: 576px) {
  .navlinks-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 25px;
    gap: 8px;
  }
}

.copyright {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  text-align: center;
  margin-top: 1rem;
  color: #777;
  font-weight: bold;
  margin-bottom: 10px;
  text-decoration: underline;
  text-underline-offset: 7px;
}

.copyright:hover {
  color: #ff7913;
}
